html {
  overflow: -moz-scrollbars-vertical;
  overflow-y: scroll;
  ::-webkit-scrollbar {
    width: 10px; } }
body {
  font-family: 'Helvetica';
  height: 100%;
  &.active {
    overflow: hidden;
    position: fixed; } }

.flex {
  display: flex; }

.h-100 {
  height: 100%; }

.out {
  display: flex;
  flex-direction: column;
  min-height: 100vh; }
.content-area {
  flex: 1 0 auto; }
.footer {
  flex: 0 0 auto; }

.content-area {
  overflow: hidden !important;
  @include r($xs) {
    padding-top: 100px; } }

.container, .section > .vc_column_container {
  max-width: 1200px !important;
  min-width: 1200px !important;
  width: 100% !important;
  box-sizing: border-box;
  margin-left: auto !important;
  margin-right: auto !important;
  float: none;
  @include r(1200) {
    padding: 0 20px !important;
    min-width: inherit !important; }
  @include r($xs) {
    padding: 0 40px !important; } }

.without-top-padding {
  padding-top: 0; }

.small-container > .vc_column_container {
  max-width: 1000px;
  width: 100% !important;
  box-sizing: border-box;
  margin-left: auto !important;
  margin-right: auto !important;
  float: none; }

p:empty {
  display: none; }



@include r($xs) {
  .hidden-xs {
    display: none !important; } }
@include rmin(768) {
  .hidden-md {
    display: none !important; } }


.heading {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-bottom: 40px;
  h2 {
    text-align: center;
    text-transform: uppercase;
    font-size: 31px;
    letter-spacing: 1.94px;
    color: $dark-blue;
    margin-bottom: 15px;
    font-weight: normal;
    min-height: 31px;
    max-height: 31px;
    @include r($xs) {
      font-size: 25px;
      letter-spacing: 1.56; } }
  .line {
    width: 60px;
    height: 5px;
    background: $blue;
    border-radius: 2px;
    position: relative;
    z-index: 100;
    &__thin {
      height: 1px;
      background: $cloudy-blue;
      position: absolute;
      z-index: -10;
      width: 0px;
      transform: translateY(1.9px);
      left: 60px;
      transition: all 2s cubic-bezier(0.74, 0.01, 0.29, 1); } }
  &__page {
    background: $grey;
    padding: 45px;
    margin-bottom: 40px;
    min-height: 140px;
    max-height: 140px;
    .heading {
      margin-bottom: 0; } } }


.text {
  background: $grey;
  padding: 115px 0;
  @include r($xs) {
    padding: 60px 0; }
  p {
    color: $dark-blue;
    font-size: 18px;
    letter-spacing: 1.13px;
    line-height: 23px;
    &:first-child {
      margin-bottom: 26px; }
    @include r($xs) {
      font-size: 14px;
      line-height: 18px; } } }

#map-mobile {
  width: 100%;
  height: 400px;
  display: none;
  @include r($md) {
    display: block; } }


.logo {
  width: 100%;
  display: flex;
  text-decoration: none;
  min-width: 118px;
  max-width: 118px;
  min-height: 50px;
  max-height: 50px;
  h1 {
    font-size: 27px;
    font-family: 'Segoe Ui';
    color: $dark-blue;
    font-weight: 600;
    display: inline-block;
    span {
      font-size: 10px;
      letter-spacing: 2.37px;
      text-align: right;
      font-weight: normal;
      display: block; } } }

.banner {
  border-radius: 6px;
  background-color: #f9f9fd;
  width: 100%;
  padding: 0 85px;
  height: 215px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  text-decoration: none;
  flex-wrap: wrap;
  transition: all .3s cubic-bezier(0.74, 0.01, 0.29, 1);
  @include r(1200) {
    margin-bottom: 20px; }
  @include r($xs) {
    padding: 0 15px; }
  &:hover {
    box-shadow: 0 28px 60px 0 rgba(191, 204, 226, 0.24);
    background-color: $white;
    div {
      background: $dark-blue;
      color: $white; } }
  p {
    font-size: 24px;
    letter-spacing: 1.5px;
    color: $dark-blue;
    text-transform: uppercase;
    @include r($xs) {
      font-size: 20px; } }
  div {
    background-color: $blue;
    color: $white;
    font-size: 13px;
    width: 60px;
    height: 60px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    text-decoration: none;
    transition: all .6s cubic-bezier(0.74, 0.01, 0.29, 1);
    @include r($xs) {
      width: 40px;
      height: 40px; }
    i {
      font-weight: 600; } }
  &.right {
    flex-direction: row-reverse;
    div i {
      transform: rotate(-180deg);
      position: relative;
      right: 5px; } } }
